body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header .name, .Header .title {
  font-weight: 100;
  text-transform: uppercase;
  text-align: center; }

.Header .name {
  font-size: 31pt; }
  .Header .name .first {
    font-style: normal;
    color: #45818e; }
  .Header .name .last {
    color: #666; }

.Header .title {
  font-size: 18pt; }

.Contact .header {
  font-weight: 100;
  text-transform: uppercase;
  text-align: center; }

.Contact .header {
  margin: 25% 0 0 0;
  padding: 0 0 5% 0;
  font-size: 14pt;
  border-bottom: solid 1pt; }

.Contact {
  font-style: normal; }
  .Contact .list {
    font-size: 10pt; }
    .Contact .list .item {
      margin: 2% 0 0 0; }

.Summary .header {
  font-weight: 100;
  text-transform: uppercase;
  text-align: center; }

.Summary .header {
  margin: 25% 0 0 0;
  padding: 0 0 5% 0;
  font-size: 14pt;
  border-bottom: solid 1pt; }

.Summary .content {
  font-size: 10pt;
  margin: 0 0 0 0;
  text-align: justify; }

.Skills .header {
  font-weight: 100;
  text-transform: uppercase;
  text-align: center; }

.Skills .header {
  margin: 25% 0 0 0;
  padding: 0 0 5% 0;
  font-size: 14pt;
  border-bottom: solid 1pt; }

.Skills .skill-item .type {
  font-size: 10pt;
  margin: 0 0 0 0; }

.Skills .skill-item .type-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 0 0 0 0;
  margin: 0 0 10% 0;
  list-style: none;
  font-size: 10pt; }
  .Skills .skill-item .type-list .type-item {
