.Employment .header {
  font-weight: 100;
  text-transform: uppercase;
  text-align: center; }

.Employment .header {
  margin: 10% 0 0 0;
  padding: 0 0 5% 0;
  font-size: 18pt;
  color: #45818e;
  border-bottom: solid 1pt; }

.Employment .item hgroup .position {
  font-weight: 100;
  margin: 0 0 0 0;
  font-size: 14pt; }

.Employment .item hgroup .employer {
  font-weight: 100;
  margin: 1% 0 0 0;
