  color: #888; }

.Employment .item hgroup .location {
  margin: 1% 0 0 0;
  font-size: 10pt; }

.Employment .item .duties {
  font-size: 10pt;
  text-align: justify; }

.Education .header {
  font-weight: 100;
  text-transform: uppercase;
  text-align: center; }

.Education .header {
  margin: 25% 0 0 0;
  padding: 0 0 5% 0;
  font-size: 14pt;
  border-bottom: solid 1pt; }

.Education .item {
  margin: 0 0 10% 0; }
  .Education .item .institution {
