.Resume {
  font-family: Comfortaa; }
  .Resume .desktop {
    display: none; }
  .Resume .list {
    list-style: none;
    padding-left: 0; }
  @media (min-width: 480px) {
    .Resume .desktop {
      display: -webkit-flex;
      display: flex; }
      .Resume .desktop .left {
        -webkit-flex: 5 1;
                flex: 5 1;
        margin: 0 5% 0 5%; }
      .Resume .desktop .right {
        -webkit-flex: 3 1;
                flex: 3 1;
        padding: 0 5% 0 5%;
        background: #134f5cff;
        color: #fff; } }

